import { Card, CardContent, CardMedia, Grid, makeStyles, Paper, Typography } from '@material-ui/core';
import React from 'react';
import img1 from '../images/img1.jpeg';
import img2 from '../images/img2.jpeg';
import img3 from '../images/img3.jpeg';
import img4 from '../images/img4.jpeg';
import img5 from '../images/img5.jpeg';
import img6 from '../images/img6.jpeg';
import img7 from '../images/img7.jpeg';
import img8 from '../images/img8.jpeg';

const useStyle = makeStyles(theme => ({
    card: {
        borderRadius: 4,
        width: '100%',
    },
    img: {
        height: 0,
        paddingTop: '56.25%', // 16:9
        objectFit: 'contain'
    }
}))

export default function Feeds(props) {

    const imgArr = [img1, img2, img3, img4, img5, img6, img7, img8];
    const classes = useStyle();

    return (
        <>
            <Grid item xs={12}>
                <Typography color='primary'>本店新年期間專營合時年花。</Typography>
                <Typography color='primary'>擁有逾五十年經驗，由上一代經營至今，亦提供本地栽種的百合。悉心栽培，本著「為顧客開心過新年」為宗旨，為每位顧客提供優質服務。</Typography>
                <Typography color='primary'>本店平日亦有時令瓜菜出售。</Typography>
                <Typography color='primary'>在農曆新年前，批發各種本地栽種的百合花以及本地蟹爪菊。</Typography>
                <Typography color='primary'>歡迎查詢​。</Typography>
                <Typography color='primary'>備有蟹爪菊及多個百合品種年花。不論是大家庭，小家庭或是辦公室，也可充滿濃厚的節日氣氛。</Typography>
                <Typography color='primary'>歡迎查詢報價</Typography>
            </Grid>
            {imgArr.map((image, index) => (
                <Grid item sm={6} xs={12} className='p-2' key={index}>
                    <Card className={classes.card}>
                        <CardMedia
                            className={classes.img}
                            image={image}
                            title="map"
                        />
                        <CardContent>
                            <Typography variant="body2" color="textSecondary" component="p" className={`py-2 ${classes.link}`}></Typography>
                        </CardContent>
                    </Card>
                </Grid>
            ))}
        </>
    )
}